.daterangepicker {
    position: absolute;
    color: inherit;
    background-color: #fff;
    border-radius: 4px;
    border: 1px solid #ddd;
    width: 278px;
    max-width: none;
    padding: 0;
    margin-top: 7px;
    top: 100px;
    left: 20px;
    z-index: 3001;
    display: none;
    font-family: arial;
    font-size: 15px;
    line-height: 1em;
}

/* .daterangepicker:before,
.daterangepicker:after {
    position: absolute;
    display: inline-block;
    border-bottom-color: rgba(0, 0, 0, 0.2);
    content: '';
} */

.daterangepicker:before {
    top: -7px;
    border-right: 7px solid transparent;
    border-left: 7px solid transparent;
    border-bottom: 7px solid #ccc;
}

.daterangepicker:after {
    top: -6px;
    border-right: 6px solid transparent;
    border-bottom: 6px solid #fff;
    border-left: 6px solid transparent;
}

.daterangepicker.opensleft:before {
    right: 9px;
}

.daterangepicker.opensleft:after {
    right: 10px;
}

.daterangepicker.openscenter:before {
    left: 0;
    right: 0;
    width: 0;
    margin-left: auto;
    margin-right: auto;
}

.daterangepicker.openscenter:after {
    left: 0;
    right: 0;
    width: 0;
    margin-left: auto;
    margin-right: auto;
}

.daterangepicker.opensright:before {
    left: 9px;
}

.daterangepicker.opensright:after {
    left: 10px;
}

.daterangepicker.drop-up {
    margin-top: -7px;
}

.daterangepicker.drop-up:before {
    top: initial;
    bottom: -7px;
    border-bottom: initial;
    border-top: 7px solid #ccc;
}

.daterangepicker.drop-up:after {
    top: initial;
    bottom: -6px;
    border-bottom: initial;
    border-top: 6px solid #fff;
}

.daterangepicker.single .daterangepicker .ranges,
.daterangepicker.single .drp-calendar {
    float: none;
}

.daterangepicker.single .drp-selected {
    display: none;
}

.daterangepicker.show-calendar .drp-calendar {
    display: block;
    padding: 20px !important;
}

.daterangepicker.show-calendar .drp-buttons {
    display: block;
}

.daterangepicker.auto-apply .drp-buttons {
    display: none;
}

.daterangepicker .drp-calendar {
    display: none;
    max-width: 270px;
}

.daterangepicker .drp-calendar.left {
    padding: 8px 0 8px 8px;
}

.daterangepicker .drp-calendar.right {
    padding: 8px;
}

.daterangepicker .drp-calendar.single .calendar-table {
    border: none;
}

.daterangepicker .calendar-table .control ul {
    list-style: none;
    display: inline-block;
    width: 100%;
    padding: 0;
    margin: 0;
}

.daterangepicker .calendar-table .control ul > li {
    float: left;
    white-space: nowrap;
    text-align: center;
    vertical-align: middle;
    min-width: 32px;
    width: 32px;
    height: 32px;
    line-height: 32px;
    font-size: 12px;
    border-radius: 4px;
    border: 1px solid transparent;
    white-space: nowrap;
    cursor: pointer;
}

.daterangepicker .calendar-table .control ul > li:first-child {
    float: left;
}
.daterangepicker .calendar-table .control ul > li:last-child {
    float: right;
}

.daterangepicker .calendar-table .control .month {
    width: calc(100% - 70px);
}
.daterangepicker .calendar-table .control .month > button {
    border-radius: 4px;
    width: 100%;
    border: none;
    padding: 6px;
    background: #fff;
    cursor: pointer;
    font-weight: bold;
    font-size: 12px;
    line-height: 1.25rem;
}

.daterangepicker .calendar-table .control .month > button:active {
    -webkit-box-shadow: none;
    box-shadow: none;
    -webkit-transform: translateY(1px);
    transform: translateY(1px);
}

.daterangepicker .calendar-table .control .month > button:hover {
    background: #eee;
}

.daterangepicker .calendar-table .control .month > button.active {
    background-color: #0061ff;
    color: #fff;
}

.daterangepicker .calendar-table .control .month > button.active:hover {
    opacity: 0.8;
}

.daterangepicker .calendar-table .next span,
.daterangepicker .calendar-table .prev span {

    display: inline-block;
    /* position: absolute; */
    margin-top: 8px;
    padding: 4px;
    border: solid #858c9e;
    border-width: 0 2px 2px 0;
}

.daterangepicker .calendar-table .next span {
    transform: rotate(-45deg);
    -webkit-transform: rotate(-45deg);
}

.daterangepicker .calendar-table .prev span {
    transform: rotate(135deg);
    -webkit-transform: rotate(135deg);
}

.daterangepicker .calendar-table th,
.daterangepicker .calendar-table td {
    white-space: nowrap;
    text-align: center;
    vertical-align: middle;
    min-width: 32px;
    width: 32px;
    height: 32px;
    line-height: 32px;
    font-size: 12px;
    border-radius: 4px;
    border: 1px solid transparent;
    white-space: nowrap;
    cursor: pointer;
}

.daterangepicker .calendar-table {
    border: 1px solid #fff;
    border-radius: 4px;
    background-color: #fff;
}

.daterangepicker .calendar-table table {
    width: 246px;
    height: 246px;
    margin: 0;
    border-spacing: 0;
    border-collapse: collapse;
}

.daterangepicker .calendar-table table > tbody.month-selection > tr > td {
    width: 50px;
    height: 50px;
}

.daterangepicker .calendar-table table > tbody.month-selection > tr > td.selected {
    background-color: #0061ff;
    color: #fff;
}

.daterangepicker .calendar-table table > tbody.month-selection > tr > td:hover {
    background-color: #eee;
    border-color: transparent;
    color: inherit;
}

.daterangepicker td.available:hover,
.daterangepicker th.available:hover {
    background-color: #eee;
    border-color: transparent;
    color: inherit;
}

.daterangepicker td.week,
.daterangepicker th.week {
    font-size: 80%;
    color: #ccc;
}

.daterangepicker td.off,
.daterangepicker td.off.in-range,
.daterangepicker td.off.start-date,
.daterangepicker td.off.end-date {
    background-color: #fff;
    border-color: transparent;
    color: #999;
}

.daterangepicker td.in-range {
    background-color: #e6f0ff;
    border-color: transparent;
    color: #000;
    border-radius: 0;
}

.daterangepicker td.weekend {
    color: #fe3131;
}

/* .daterangepicker td.sunday {
    color: #fe3131;
} */

/* .daterangepicker td.saturday {
    color: inherit;
} */

.daterangepicker td.start-date {
    border-radius: 4px 0 0 4px;
}

.daterangepicker td.end-date {
    border-radius: 0 4px 4px 0;
}

.daterangepicker td.start-date.end-date {
    border-radius: 4px;
}

.daterangepicker td.active,
.daterangepicker td.active:hover {
    background-color: #0061ff;
    border-color: transparent;
    color: #fff;
}

.daterangepicker select {
    margin-right: 8px;
    width: 70px;
    height: 28px;
    font-size: 12px;
    line-height: 29px;
    padding: 0 28px 0 12px !important;
    border-radius: 3px;
    -webkit-border-radius: 3px;
    -webkit-appearance: none;
    appearance: none;
    background: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' width='100' height='100'><polygon points='0,0 100,0 50,50'/></svg>") no-repeat;
    background-size: 10px;
    background-position: calc(100% - 10px) calc(100% - 4px);
    background-repeat: no-repeat;
}


.daterangepicker td.disabled,
.daterangepicker option.disabled {
    color: #999;
    cursor: not-allowed;
    text-decoration: line-through;
}


.daterangepicker select.monthselect,
.daterangepicker select.yearselect {
    cursor: default;
}



.daterangepicker select.hourselect,
.daterangepicker select.minuteselect,
.daterangepicker select.secondselect,
.daterangepicker select.ampmselect {
    width: 60px;
    margin: 0 auto;
    display: initial;
}

.daterangepicker .calendar-time {
    text-align: center;
    margin: 4px auto 0 auto;
    line-height: 30px;
    position: relative;
}

.daterangepicker .calendar-time select.disabled {
    color: #ccc;
    cursor: not-allowed;
}

.daterangepicker .drp-buttons {
    clear: both;
    text-align: right;
    padding: 15px;
    border-top: 1px solid #ddd;
    display: none;
    line-height: 12px;
    vertical-align: middle;
}

.daterangepicker .drp-selected {
    display: inline-block;
    font-size: 12px;
    padding-right: 8px;
}

.daterangepicker .drp-buttons .btn {
    border-radius: 3px !important;
    -webkit-border-radius: 3px !important;
    margin-left: 4px !important;
    font-size: 12px !important;
    /* font-weight: bold; */
    padding: 8px 12px !important;
    -webkit-appearance: none !important;
    appearance: none !important;
    display: inline !important;
    line-height: initial !important;
    height: auto !important;
}

.daterangepicker .drp-buttons .btn:active {
    -webkit-box-shadow: none;
    box-shadow: none;
    -webkit-transform: translateY(1px);
    transform: translateY(1px);
}

.daterangepicker .drp-buttons .btn:disabled {
    background: #eee !important;
    color: #cacaca !important;
    border: #ebebeb 1px solid !important;
}

.daterangepicker .drp-buttons .btn:hover {
    opacity: 0.8;
}

.daterangepicker .drp-buttons .btn.apply {
    background: #0061ff;
    border: #0061ff 1px solid;
    color: #fff
}

.daterangepicker .drp-buttons .btn.cancel {
    background: #ffffff;
    border: #767676 1px solid;
    color: #191919
}


.daterangepicker.show-ranges.single.rtl .drp-calendar.left {
    border-right: 1px solid #ddd;
}

.daterangepicker.show-ranges.single.ltr .drp-calendar.left {
    border-left: 1px solid #ddd;
}

.daterangepicker.show-ranges.rtl .drp-calendar.right {
    border-right: 1px solid #ddd;
}

.daterangepicker.show-ranges.ltr .drp-calendar.left {
    border-left: 1px solid #ddd;
}

.daterangepicker .ranges {
    float: none;
    text-align: left;
    margin: 0;
}

.daterangepicker.show-calendar .ranges {
    margin-top: 8px;
}

.daterangepicker .ranges ul {
    list-style: none;
    margin: 0 auto;
    padding: 0;
    width: 100%;
}

.daterangepicker .ranges li {
    font-size: 12px;
    padding: 8px 12px;
    cursor: pointer;
}

.daterangepicker .ranges li:hover {
    background-color: #eee;
}

.daterangepicker .ranges li.active {
    background-color: #0061ff;
    color: #fff;
}


@media (max-width: 564px) {
    .daterangepicker .drp-buttons .btn {
        width: 100%;
        margin-top: 5px !important;
        margin-bottom: 5px !important;
    }

    .daterangepicker .drp-selected {
        display: grid;
        padding-right: 0;
        align-items: center;
        justify-items: center;
        margin-bottom: 8px;
    }
}

/*  Larger Screen Styling */
@media (min-width: 564px) {
    .daterangepicker {
        width: max-content;
    }

    .daterangepicker .ranges ul {
        width: 140px;
    }

    .daterangepicker.single .ranges ul {
        width: 100%;
    }

    .daterangepicker.single .drp-calendar.left {
        clear: none;
    }

    .daterangepicker.single .ranges,
    .daterangepicker.single .drp-calendar {
        float: left;
    }

    .daterangepicker {
        direction: ltr;
        text-align: left;
    }

    .daterangepicker .drp-calendar.left {
        clear: left;
        margin-right: 0;
    }

    .daterangepicker .drp-calendar.left .calendar-table {
        border-right: none;
        border-top-right-radius: 0;
        border-bottom-right-radius: 0;
    }

    .daterangepicker .drp-calendar.right {
        margin-left: 0;
        padding-left: 0 !important;
    }

    .daterangepicker .drp-calendar.right .calendar-table {
        border-left: none;
        border-top-left-radius: 0;
        border-bottom-left-radius: 0;
    }

    .daterangepicker .drp-calendar.left .calendar-table {
        padding-right: 8px;
    }

    .daterangepicker .ranges,
    .daterangepicker .drp-calendar {
        float: left;
    }
}

@media (min-width: 730px) {
    .daterangepicker .ranges {
        width: auto;
    }

    .daterangepicker .ranges {
        float: left;
    }

    .daterangepicker.rtl .ranges {
        float: right;
    }

    .daterangepicker .drp-calendar.left {
        clear: none !important;
    }
}

.daterangepicker.dark {
    border: 1px solid #14171d;
    background-color: #232730;
    color: #fff;
}

.daterangepicker.dark:after {
    border-bottom: 6px solid #232730;
}

.daterangepicker.dark select {
    border-color: #3d4456;
    color: #fff;
    background: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' width='100' height='100' fill='gray'><polygon points='0,0 100,0 50,50'/></svg>") no-repeat;
    background-size: 10px;
    background-position: calc(100% - 10px) calc(100% - 4px);
    background-repeat: no-repeat;
}

.daterangepicker.dark select > option {
    background: #3d4456;
    border-color: #3d4456;
    color: #fff;
}

.daterangepicker.dark .drp-buttons .btn.apply {
    background-color: #3493ff;
    border-color: #3493ff;
    color: #fff;
}

.daterangepicker.dark .drp-buttons .btn.cancel {
    background-color: #232730;
    border-color: #232730;
    color: #fff;
}

.daterangepicker.dark.show-ranges.ltr .drp-calendar.left {
    border-left: 1px solid #14171d;
}

.daterangepicker.dark .drp-buttons {
    border-top: 1px solid #14171d;
}

.daterangepicker.dark .calendar-table {
    border: none;
    background-color: #232730;
}
.daterangepicker.dark td.off, .daterangepicker.dark td.off.in-range, .daterangepicker.dark td.off.start-date, .daterangepicker.dark td.off.end-date {
    background-color: #232730;
    color: #999;
}

.daterangepicker.dark td.active, .daterangepicker.dark td.active:hover {
    background-color: #3493ff;
    color: #fff;
}

.daterangepicker.dark td:hover {
    background-color: #3d414a;
}

.daterangepicker.dark .calendar-table .control .month > button {
    background: #232730;
    color: #fff;
}

.daterangepicker.dark .calendar-table .control .month > button:hover {
    background: #3d414a;
    color: #fff;
}

.daterangepicker.dark .calendar-table .control .month > button.active {
    background-color: #3493ff;
    color: #fff;
}

.daterangepicker.dark .calendar-table .control .month > button.active:hover {
    background: #8ec3ff;
    color: #fff !important;
    opacity: 1;
}

.daterangepicker.dark .calendar-table table > tbody.month-selection > tr > td:hover {
    background-color: #3d414a;
}

.daterangepicker .calendar-table table > tbody.month-selection > tr > td.selected {
    background-color: #3493ff;
    color: #fff;
}

.daterangepicker.dark .ranges li:hover {
    background-color: #3d414a;
}

.daterangepicker.dark .ranges li.active {
    background-color: #3493ff !important;
    color: #fff;
}